import axios from 'axios'

export default {
  novaRemesa (filtro) {
    return axios.post('/v1/spc/nova-remessa', filtro)
  },
  gerarRemesa (clientes, tipo) {
    return axios.post(`/v1/spc/gerar-remessa/${tipo}`, clientes)
  },
  gerarRemesaOnline (clientes, tipo) {
    return axios.post(`/v1/spc/incluir-excluir-list/${tipo}`, clientes)
  },
  getResultadoRemessa (remessaId) {
    return axios.get(`/v1/spc/resultado-remessa/${remessaId}`)
  },
  pagination (pageSize = 10, pageNo = 0, sortBy = 'dataCadastro', filtros) {
    return axios.post('/v1/spc/pagination', {
      pageSize, pageNo, sortBy, filtros
    })
  },
  incluirOrExcluir (filtro) {
    return axios.post('/v1/spc/incluir-excluir', filtro)
  },
  novaConsultaSPC (cpf, lojaId) {
    return axios.get(`/v1/spc/nova-consulta-spc/${cpf}/${lojaId}`)
  },
  getDetalheConsultaSPC (consultaId) {
    return axios.get(`/v1/spc/consulta-spc/${consultaId}`)
  },
  findAllConsultaSPC (cpf) {
    return axios.get(`/v1/spc/findall-consulta-spc/${cpf}`)
  },
  findUltimoHistoricoClienteSPC (clienteId) {
    return axios.get(`/v1/spc/ultimo-historico-clientespc/${clienteId}`)
  }
}
