import axios from 'axios'

export default {
  processarIndividual (repasse) {
    return axios.post('/v1/repasse/processar-individual', repasse)
  },
  calculaRepasseIndividual (filtro) {
    return axios.post(`/v1/repasse/calcula-repasse-individual`, filtro)
  },
  processarGeral (repasse) {
    return axios.post('/v1/repasse/processar-geral', repasse)
  },
  calculaRepasseGeral (filtro) {
    return axios.post(`/v1/repasse/calcula-repasse-geral`, filtro)
  },
  calculaReembolsoRepasse (filtro) {
    return axios.post(`/v1/repasse/calcula-reembolso-repasse`, filtro)
  },
  uploadDocumento (id, formData) {
    return axios.post(`/v1/repasse/${Number(id)}/upload-documento`, formData, {})
  },
  downloadAnexo (id) {
    axios({
      url: `/v1/repasse/${id}/anexo`,
      method: 'GET',
      responseType: 'blob'
    })
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', response.headers['content-disposition'].split(';')[1].split('=')[1])
        document.body.appendChild(link)
        link.click()
      })
  },
  gerarArquivoTEDs (filtro) {
    return axios({
      url: `/v1/repasse/gerar-arquivo-teds`,
      method: 'POST',
      // responseType: 'blob',
      data: filtro
    })
  }
}
