import axios from 'axios'

export default {
  salvar (rede) {
    if (rede.id) {
      return this.update(rede)
    } else {
      return this.novo(rede)
    }
  },
  findAll () {
    return axios.get('/v1/rede')
  },
  pagination (_pagination, filtros) {
    let _pageNo = _pagination.page == null ? 0 : _pagination.page - 1
    let _sortBy = _pagination.sortBy || 'nome'
    return axios.post('/v1/rede/pagination', {
      pageSize: _pagination.rowsPerPage,
      pageNo: _pageNo,
      sortBy: _sortBy,
      direction: _pagination.descending,
      filtros
    })
  },
  novo (rede) {
    return axios.post('/v1/rede', rede)
  },

  update (rede) {
    return axios.post(`/v1/rede/${rede.id}`, rede)
  },

  delete (id) {
    return axios.delete(`/v1/rede/${id}`)
  },

  getById (id) {
    return axios.get(`/v1/rede/${id}`)
  },
  getRegrafinanceiraVigenteById (redeId) {
    return axios.get(`/v1/rede/regrafinanceira-vigente/${redeId}`)
  }
}
