import axios from 'axios'

export default {
  calculaReembolsoRepasse (filtro) {
    return axios.post(`/v1/reembolso/calcula-reembolso-repasse`, filtro)
  },
  consultarBoletosReembolso (filtro) {
    return axios.post(`/v1/reembolso/boletos`, filtro)
  },
  efetuarReembolso (form) {
    return axios.post(`/v1/reembolso/efetuar-reembolso-repasse`, form)
  }
}
