import axios from 'axios'

const NOTIFICACAO = '/v1/notificacao'

export default {
  getById (id) {
    return axios.get(`${NOTIFICACAO}/${id}`)
  },
  marcarComoVisualizado (id) {
    return axios.post(`${NOTIFICACAO}/visualizado/${id}`)
  },
  paginationLoja (filtros) {
    return axios.post(`${NOTIFICACAO}/pagination-loja`, {
      pageSize: 10, pageNo: 0, sortBy: 'notificacao.dataCadastro', filtros
    })
  },
  paginationCliente (filtros) {
    return axios.post(`${NOTIFICACAO}/pagination-cliente`, {
      pageSize: 10, pageNo: 0, sortBy: 'notificacao.dataCadastro', filtros
    })
  },
  paginationPratico (filtros) {
    return axios.post(`${NOTIFICACAO}/pagination-pratico`, {
      pageSize: 10, pageNo: 0, sortBy: 'notificacao.dataCadastro', filtros
    })
  }
}
