import axios from 'axios'

export default {
  findAll () {
    return axios.get('/v1/beneficiario/')
  },
  findAllBanco () {
    return axios.get('/v1/beneficiario/banco')
  },
  getById (id) {
    return axios.get(`/v1/beneficiario/${id}`)
  },
  salvar (beneficiario) {
    return axios.post(`/v1/beneficiario/`, beneficiario)
  },
  delete (id) {
    return axios.delete(`/v1/beneficiario/${id}`)
  }
}
