import axios from 'axios'

export default {
  salvar (lancamentoExtra) {
    return this.novo(lancamentoExtra)
  },
  findAll (lojaId) {
    return axios.get(`/v1/lancamentoextra/loja/${lojaId}`)
  },
  novo (lancamentoExtra) {
    return axios.post('/v1/lancamentoextra', lancamentoExtra)
  },
  delete (id) {
    return axios.delete(`/v1/lancamentoextra/${id}`)
  },
  uploadDocumento (id, formData) {
    return axios.post(`/v1/lancamentoextra/${Number(id)}/upload-documento`, formData, {})
  },
  downloadAnexo (id) {
    axios({
      url: `/v1/lancamentoextra/${id}/anexo`,
      method: 'GET',
      responseType: 'blob'
    })
      .then((response) => {
        const url = window.URL.createObjectURL(new Blob([response.data]))
        const link = document.createElement('a')
        link.href = url
        link.setAttribute('download', response.headers['content-disposition'].split(';')[1].split('=')[1])
        document.body.appendChild(link)
        link.click()
      })
  }
}
