import axios from 'axios'

export default {
  findAllRenegociacao (clienteId, lojaId) {
    return axios.get(`/v1/renegociacao/${clienteId}/${lojaId}`)
  },
  findCarnesParaAcordo (clienteId, lojaId) {
    return axios.get(`/v1/renegociacao/carnes/${clienteId}/${lojaId}`)
  },
  efetuaRenegociacao (clienteId, lojaId, form) {
    return axios.post(`/v1/renegociacao/renegociar/${clienteId}/${lojaId}`, form)
  },
  cancelarRenegociacao (form) {
    return axios.post(`/v1/renegociacao/cancelar`, form)
  }
}
