import axios from 'axios'

export default {
  salvar (plano) {
    if (plano.id) {
      return this.update(plano)
    } else {
      return this.novo(plano)
    }
  },
  findAll () {
    return axios.get('/v1/planopagamento')
  },
  pagination (_pagination, filtros) {
    let _pageNo = _pagination.page == null ? 0 : _pagination.page - 1
    let _sortBy = _pagination.sortBy || 'qtdParcelas'
    return axios.post('/v1/planopagamento/pagination', {
      pageSize: _pagination.rowsPerPage,
      pageNo: _pageNo,
      sortBy: _sortBy,
      direction: _pagination.descending,
      filtros
    })
  },
  novo (plano) {
    return axios.post('/v1/planopagamento', plano)
  },

  update (plano) {
    return axios.post(`/v1/planopagamento/${plano.id}`, plano)
  },

  delete (id) {
    return axios.delete(`/v1/planopagamento/${id}`)
  },

  getById (id) {
    return axios.get(`/v1/planopagamento/${id}`)
  }
}
