import axios from 'axios'

export default {
  salvar (empresa) {
    if (empresa.id) {
      return this.update(empresa)
    } else {
      return this.novo(empresa)
    }
  },
  findAll () {
    return axios.get('/v1/empresa')
  },
  pagination (pageSize = 10, pageNo = 0, sortBy = 'nome', filtros) {
    return axios.post('/v1/empresa/pagination', {
      pageSize, pageNo, sortBy, filtros
    })
  },
  novo (empresa) {
    return axios.post('/v1/empresa/', empresa)
  },

  update (empresa) {
    return axios.post(`/v1/empresa/${empresa.id}`, empresa)
  },

  delete (id) {
    return axios.delete(`/v1/empresa/${id}`)
  },

  getById (id) {
    return axios.get(`/v1/empresa/${id}`)
  },

  uploadLogo (id, formData) {
    return axios.post(`/v1/empresa/${Number(id)}/upload-logo`, formData, {})
  },
  getRegrafinanceiraVigenteById (empresaId) {
    return axios.get(`/v1/empresa/regrafinanceira-vigente/${empresaId}`)
  }
}
