import axios from 'axios'

export default {
  iniciarCobranca (filtro) {
    return axios.post('/v1/cobranca/iniciar-cobranca', filtro)
  },
  pagination (_pagination, filtros) {
    let _pageNo = _pagination.page == null ? 0 : _pagination.page - 1
    let _sortBy = _pagination.sortBy
    return axios.post('/v1/cobranca/ocorrencias-pagination', {
      pageSize: _pagination.rowsPerPage,
      pageNo: _pageNo,
      sortBy: _sortBy,
      direction: _pagination.descending,
      filtros
    })
  },
  salvarOcorrencia (form) {
    return axios.post('/v1/cobranca/ocorrencia', form)
  },
  getDadosCobrancaCliente (clienteId, lojaId) {
    return axios.get(`/v1/cobranca/dados-cobranca/${clienteId}/${lojaId}`)
  },
  salvarConfigMailing (form) {
    return axios.post('/v1/cobranca/config-mailing', form)
  },
  getConfigMailing (usuarioId) {
    return axios.get(`/v1/cobranca/config-mailing/usuario/${usuarioId}`)
  },
  geraRelatorioMailing (filtros) {
    return axios({
      url: `/v1/cobranca/mailing`,
      method: 'POST',
      responseType: 'blob',
      data: filtros
    })
  }
}
