import axios from 'axios'

export default {
  async salvar (regra) {
    await axios.post('/v1/regrafinanceira', regra)
  },
  findAll () {
    return axios.get('/v1/regrafinanceira')
  },
  pagination (pageSize = 10, pageNo = 0, sortBy = 'id', filtros) {
    return axios.post('/v1/regrafinanceira/pagination', {
      pageSize, pageNo, sortBy, filtros
    })
  },
  delete (id) {
    return axios.delete(`/v1/regrafinanceira/${id}`)
  },
  getById (id) {
    return axios.get(`/v1/regrafinanceira/${id}`)
  }
}
