import axios from 'axios'

export default {
  uploadArquivoRetorno (cedenteId, formData) {
    return axios.post(`/v1/retornobanco/upload-arquivo-retorno/${cedenteId}`, formData, {})
  },
  getUltimoArquivoRetorno (cedenteId) {
    return axios.get(`/v1/retornobanco/ultimo-retorno/${cedenteId}`)
  }
}
