import axios from 'axios'

export default {
  gerarRPS (formData) {
    return axios.post('/v1/rps', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
  },
  paginationLote (pageSize = 20, pageNo = 0, sortBy = 'dhEnvio', filtros) {
    return axios.post('/v1/rps/pagination-lote', {
      pageSize, pageNo, sortBy, filtros
    })
  },
  getLoteRps (loteId) {
    return axios.get(`/v1/rps/${loteId}`)
  },
  proximoNumeroRps () {
    return axios.get('/v1/rps/proximo-numero-rps')
  }
}
